import React, { useState } from 'react'
import { useMutation } from "react-apollo-hooks";
import { gql } from "apollo-boost";
import {
  Typography,
  Form,
  Input,
  Button,
  Select,
  DatePicker,
} from 'antd';
import { GET_ARTICLES } from './ArticleList'


const ADD_ARTICLE = gql`
  mutation addArticle($article: articles_insert_input!) {
    insert_articles(objects: [$article]) {
      returning {
        id
        title
      }
    }
  }
`;

const AddArticle = () => {
  const { Title } = Typography;
  const { TextArea } = Input;
  const { Option } = Select;

  const [articleTitle, setArticleTitle] = useState('')
  const [articleSlug, setArticleSlug] = useState('')
  const [articleTeaser, setArticleTeaser] = useState('')
  const [articleBody, setArticleBody] = useState('')
  const [articleDate, setArticleDate] = useState('2020-06-01')
  const [articleCategories, setArticleCategories] = useState([]);

  const [addArticle, { loading, error }] = useMutation(ADD_ARTICLE, {
    update: (cache, { data }) => {
      setArticleTitle("")
      setArticleSlug("")
      setArticleTeaser("")
      setArticleBody("")
      setArticleDate("")
      const existingArticles = cache.readQuery({
        query: GET_ARTICLES
      })

      const newArticle = data.insert_articles.returning[0];
      cache.writeQuery({
        query: GET_ARTICLES,
        data: {articles: [newArticle, ...existingArticles.articles]}
      });
    }
  });

  if (loading) return "loading...";
  if (error) return `error: ${error.message}`;

  return (
    <>
      <Title>Добавить новость</Title>
      <Form
        labelCol={{ span: 4 }}
        wrapperCol={{ span: 14 }}
        layout="horizontal"
        onFinish={e => {
          addArticle({
            variables: {
              "article": {
                "title": articleTitle,
                "slug": articleSlug,
                "teaser": articleTeaser,
                "body": articleBody,
                "created_at": articleDate
              }
            }
          })
        }}
      >
        <Form.Item label="Заголовок">
          <Input value={articleTitle} onChange={e => (setArticleTitle(e.target.value))} />
        </Form.Item>
        <Form.Item label="Латиница">
          <Input value={articleSlug} onChange={e => (setArticleSlug(e.target.value))} />
        </Form.Item>
        <Form.Item label="Тизер">
          <Input value={articleTeaser} onChange={e => (setArticleTeaser(e.target.value))} />
        </Form.Item>
        <Form.Item label="Текст">
          <TextArea value={articleBody} onChange={e => (setArticleBody(e.target.value))} />
        </Form.Item>
        <Form.Item label="Select">
          <Select mode="multiple" placeholder="Please select favourite colors" onChange={e => setArticleCategories(e.target.value)}>
            <Option value="3">Наука</Option>
            <Option value="2">Спорт</Option>
            <Option value="1">Бизнес</Option>
            <Option value="4">Все новости</Option>
          </Select>
        </Form.Item>
        <Form.Item label="DatePicker">
          <DatePicker />
        </Form.Item>
        <Form.Item label="Button">
          <Button type="primary" htmlType="submit">Button</Button>
        </Form.Item>
      </Form>
    </>
  )
};

export default AddArticle;